/* Additional header styles */
.header {
  @apply sticky top-0 z-50;
}

/* Ensure collection name truncates properly */
.collection-name {
  @apply truncate max-w-[150px] sm:max-w-[200px] md:max-w-[300px];
}

@media (max-width: 639px) {
  .collection-name {
    @apply max-w-[150px] truncate text-sm;
  }
}

/* Custom checkbox styles */
input[type="checkbox"] {
  @apply appearance-none w-4 h-4 rounded border border-gray-300 dark:border-gray-600 
         bg-white dark:bg-[#1B2131] text-primary
         checked:bg-primary checked:border-primary
         focus:ring-2 focus:ring-primary/30 focus:ring-offset-0
         cursor-pointer transition-colors;
  aspect-ratio: 1/1;
  min-width: 16px;
  min-height: 16px;
  max-width: 16px;
  max-height: 16px;
}

input[type="checkbox"]:checked {
  @apply bg-purple-100 border-purple-300 dark:bg-purple-900/30 dark:border-purple-700;
}

input[type="checkbox"].text-primary {
  @apply text-purple-700 dark:text-purple-400;
}

input[type="checkbox"].focus\:ring-primary:focus {
  @apply ring-purple-300 dark:ring-purple-800/50;
}

/* Card checkbox container */
.card .checkbox-container {
  @apply absolute top-2 right-2 z-30;
}

/* Ensure checkboxes are visible */
.card input[type="checkbox"] {
  @apply opacity-100 visible;
}

/* Stats values */
.stat-value {
  @apply text-base sm:text-2xl font-semibold;
}

.stat-label {
  @apply text-xs font-medium uppercase tracking-normal text-gray-600 dark:text-gray-300;
}

/* This ensures the profit value is colored but keeps the same font size */
.stat-value.profit-value {
  @apply text-green-500 dark:text-green-400;
}

.stat-value.loss-value {
  @apply text-red-500 dark:text-red-400;
}

/* Card Counter */
.card-counter {
  @apply flex items-center gap-0.5 sm:gap-1 justify-center;
}

.card-counter .material-icons {
  @apply text-xs sm:text-sm text-gray-600 dark:text-gray-300;
}

/* Settings page background */
.settings-page {
  background-color: #f3f4f6;
}

.dark .settings-page {
  background-color: #111827;
}

/* Settings mobile styles */
.mobile-settings-content > div:not(:last-child) {
  margin-bottom: 16px;
}

.mobile-settings-content > div {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

/* Settings container max width for mobile */
@media (max-width: 768px) {
  .settings-page .p-6 {
    max-width: 36rem; /* Same as max-w-xl (36rem/576px) */
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .settings-page .mobile-settings-content > div {
    width: 100%;
  }
}

/* Fix mobile content distortion */
@media (max-width: 768px) {
  /* Ensure proper scaling on mobile */
  html, body {
    -webkit-text-size-adjust: 100%;
    text-size-adjust: 100%;
  }
  
  /* Prevent horizontal overflow */
  .app, .app-content, .dashboard-page, .settings-page {
    overflow-x: hidden;
    max-width: 100vw;
  }
  
  /* Ensure buttons have minimum touch target size */
  button, 
  .btn,
  [role="button"],
  input,
  select,
  .card-stat,
  .collection-item {
    min-height: 44px;
    touch-action: manipulation;
  }
  
  /* Ensure content fits within viewport */
  .card-grid {
    width: 100%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  
  /* Prevent flex items from shrinking too much */
  .flex-1 {
    flex-shrink: 0;
    min-width: 0;
  }
  
  /* Fix settings page display */
  .settings-page .mobile-settings-content > div {
    border-radius: 0.75rem;
    margin-bottom: 1rem;
    width: 100%;
    box-sizing: border-box;
  }
  
  /* Ensure consistent spacing */
  .p-4 {
    padding: 1rem !important;
  }
  
  /* Make sure buttons display correctly */
  .settings-page button {
    width: 100%;
    height: auto;
    min-height: 44px;
    padding-top: 0.625rem;
    padding-bottom: 0.625rem;
    line-height: 1.5;
  }
  
  /* Fix for flexbox items on iOS */
  .flex {
    display: flex;
  }
  
  .flex > * {
    max-width: 100%;
  }
  
  /* Fix for label alignment */
